@import '../../../scss/variables.scss';
.table{
    width: 45rem;
    margin-top: 2rem;
    border: 0.5px solid rgba(169,188,206,0.53);
    box-shadow: 8px 11px 11px 0px rgba(169,188,206,0.53);
    background-color: rgb(238, 245, 245);

    th{
        font-size: 0.7rem;
        color: $secondary-color;
    }

    td{
        font-size: 0.6rem;
        color: $primary-color;
    }

    button{
        font-size: 0.5rem;
        letter-spacing: 1px;
        border:none;
        border-radius: 3px;
        background-color: $fourth-color;
        color: $secondary-color;
    }

    button:hover{   
        background-color: $secondary-color;
        color: $fourth-color;
    }
}