
@import '../../../scss/variables.scss';


.sign-up{
    display: flex;
    justify-content: center;
    align-items: center;

    .sign-up-layout{
        width: 50rem;
        display: flex;
        flex-direction: row;
        justify-content: space-evenly;
        align-items: center;
        margin-top: 1rem;

        form{
            width: 17rem;
            height: 24rem;
            display: flex;
            flex-direction: column;
            justify-content: flex-start;
            align-items: center;
            margin-top: 1rem;
            color: $primary-color;
            background-color: $fifth-color;
            box-shadow: 8px 11px 11px 0px rgba(169,188,206,0.53);
            
            h2{
                text-align: center;
                font-size: 1rem;
                padding: 1rem 0rem 0.5rem 0rem;
                color: $secondary-color;   
            }
        
            div{
                width:17rem;
                height: 2rem;
                display: flex;
                flex-direction: row;
                justify-content: space-between;
                align-items: center;
                flex-wrap: wrap;
                margin: 0.5rem 1rem 0.3rem 1rem;
                padding: 0rem 1rem 0rem 1rem;
        
                label{
                    font-size: 0.6rem;
                }

                input, select{
                    width: 10rem;
                    height: 1.5rem;
                    font-size: 0.4rem;
                    padding-left: 10px;
                    border:1px solid rgb(223, 232, 247);
                    border-radius: 3px;
                }
            }
            
            button{
                width: 8rem;
                height: 2rem;
                font-size: 0.6rem;
                letter-spacing: 1px;
                border:none;
                border-radius: 3px;
                color: $secondary-color;
                background-color: $fourth-color;
            }

            button:hover{
                color: $fourth-color;
                background-color: $secondary-color;
            }

            p{
                font-size: 0.5rem;
                margin: 2rem 2.3rem 2rem 2.4rem;
            }
        
        }

    }   
}